	module.exports = {"data":{"markdownRemark":{"html":"<p>Hi, I'm Dean. ðŸ‘‹</p>\n<p>My self-proclaimed title is always changing: these days I'm a <strong>UX Developer</strong>.</p>\n<p>Here's why:</p>\n<ul>\n<li>User-centered digital hospitality. I'm asking, \"How can I make this a better experience for you?\"</li>\n<li>I love to code. I create with both Figma and React; the right tool for the job.</li>\n<li>I have a sense of aesthetics, rhythm, and balance. Not trendy or flashy, but timeless and considerate.</li>\n<li>I'm a data-driven problem solver, life-long learner, design and systems-thinker.</li>\n</ul>\n<p>My work history spans multiple industries and job titles and I draw on this when designing for end users. Every experience is valuable in it's own right.</p>\n<p>I studied Psychology and Linguistics at SFU and I use these research and interviewing skills throughout my design process.</p>\n<p>And in other news? I'm from Vancouver, BC. When Iâ€™m not working on my side projects I enjoy learning the guitar, discovering new gluten-free restaurants, hiking, camping and reading.</p>\n<h4 id=\"education\"><a href=\"#education\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Education</h4>\n<ul>\n<li>Brainstation, User Experience and User Interface Design</li>\n<li>Simon Fraser University, B.A. Psychology</li>\n<li>Simon Fraser University, Certificate of Liberal Arts</li>\n</ul>\n<h2 id=\"get-in-touch\"><a href=\"#get-in-touch\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Get in touch!</h2>\n<p>Whether itâ€™s feedback on one of my projects or an idea you want to collaborate on, I would love to hear from you!</p>\n<p>Send me an email if you are interested in what I do, and we can arrange a time to chat.</p>\n<p>You can also find me on:</p>\n<ul>\n<li>LinkedIn</li>\n<li>Twitter</li>\n<li>GitHub</li>\n</ul>","headings":[{"value":"Education","depth":4},{"value":"Get in touch!","depth":2}],"frontmatter":{"date":null,"path":"/about","title":"About Me","tags":[],"toc":false}}},"pathContext":{}}

